<template>
    <div>
        <el-card class="box-card">
        <el-avatar
        shape="square"
        :size="150"
        fit="cover"
        :src="user.photo"
        >
        </el-avatar>
        <p @click="$refs.refFile.click()" class="btnImage">点击裁切图片</p>
        <input type="file" hidden ref="refFile" @change="onFileChang" >

        </el-card>

        <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        @opened="onDialogOpened"
        @closed="onDialogClosed"
        >
        <div class="images">
            <img class="images-item" :src="previewImag" ref="images-item">
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button 
            :loading="updateLoading"
            type="primary" @click="onUpdatePhoto">确 定</el-button>
        </span>
        </el-dialog>
    </div>
</template>
<script>
import 'cropperjs/dist/cropper.css';
import Cropper from 'cropperjs';
export default {
    data(){
        return{
            user:{
                email:'',
                id:null,
                intro:'',
                mobile:'',
                name:'',
                photo:''
            },
            dialogVisible:false,
            previewImag:'',      // 预览图片
            cropper:null,        // 裁切器示例
            updateLoading:false
        }
    },
    methods:{
        add(){
            console.log('1');
        },
        changes(){
            console.log('2');
        },
        // 修改头像
        onFileChang(){
            //  处理预览图片
            const file = this.$refs.refFile
            const blobData = window.URL.createObjectURL(file.files[0])
            this.previewImag = blobData
            this.dialogVisible = true
            this.$refs.refFile.value = ''
        },
        onDialogOpened(){
            const image = this.$refs['images-item']
            this.cropper = new Cropper(image, {
            aspectRatio: 1,     // 图片比列  如：3/2
            viewMode:1,         //截图框
            dragMode:'none'     //图片不可移动
            });
        },
        onDialogClosed(){
            //  对话框关闭，摧毁裁切器
            this.cropper.destroy()
        },
        onUpdatePhoto(){
            this.updateLoading = true
            //  获取裁切的图片对象
            this.cropper.getCroppedCanvas().toBlob(blob =>{
                const fd = new FormData()
                fd.append('photo',blob)
                //  请求更新用户头像，提交 fd
                // updateUserPhoto(fd).then(res =>{
                    //  关闭窗口
                    this.dialogVisible = false
                    //  1、把裁切结果的文件对象转为 blob 数据
                    this.user.photo = window.URL.createObjectURL(blob)

                    //  2、通过请求更换头像刷新
                    // this.user.photo = res.data.data.photo
                    this.updateLoading = false
                    this.$message({
                        message:'修改成功',
                        type:'success'
                    })
                // })
            })
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
            .then(_ => {
                done();
            })
            .catch(_ => {});
        }
    }
}
</script>
<style lang="less" scoped>
.btnImage{
    cursor: pointer;
}
</style>